@use "../abstracts/variables";
footer {
  padding-top: 60px;
  padding-bottom: 27px;
}
footer > .container {
  display: flex;
  justify-content: center;
}
.footer__link {
  font-family: variables.$font-family;
  font-weight: 400;
  font-size: 0.88rem;
  text-align: center;
  color: #939393;
  .heart {
    color: red;
  }
}

@media screen and (min-width: 768px) {
  .footer__link {
    font-size: 1rem;
  }
}
